<template>
  <div class="mid-number">
    <div class="title">
      <text class="icon iconfont">&#xe647;</text>
      <text class="tit-txt">{{ model.name }}</text>
    </div>
    <i-slidebar :min=model.min :max=model.max :value=value :step=model.step :disable=!model.down  @getValue="getValue"></i-slidebar>
  </div>
</template>

<script>
import ISlidebar from './i-slidebar';
export default {
  props: ['dataList'],
  data() {
    return {
      
    }
  },
  computed: {
    model() {
      return this.dataList.function
    },
    value() {
      return this.$store.state[this.model.index];
    }
  },
  components: {
    ISlidebar
  },
  methods: {
    getValue(val){
      this.publish(val)
    }
  }
}
</script>

<style scoped>
.mid-number { padding-top: 30px;}
.iconfont { font-family: iconfont2; color: white; }
.icon { margin-left: 10px; font-size: 46px;}
.title { flex-direction: row; align-items: center; height: 46px; width: 670px; padding-bottom: 10px;}
.tit-txt { color: white;  font-size: 30px;  margin-left: 20px;}

</style>



