<template>
  <div class="ui-switch" @touchstart="change">
    <div class="bkg" ref="bkg"></div>
    <div class="item"><text class="center"></text></div>
    <div class="item"><text class="center"></text></div>
  </div>
</template>

<script>
export default {
  props: ['name', 'value'],
  data() {
    return {
      val: false
    }
  },
  computed: {
    anim() {
      if(this.val) return 0
      return '140px'
    }
  },
  watch: {
    value() {
      this.val = this.value;
    },
    val() {
      this.animato();
    }
  },
  methods: {
    change() {
      this.val = this.val ? false : true;
      this.$emit('attrValue', this.val)
    },
    animato() {
      const el = this.$refs.bkg;
      this.animateTo(el, {
        'transform': `translateX(${this.anim})`
      });
    }
  },
  created() {
    this.val = this.value;
  }
}
</script>
<style scoped>
.ui-switch {
  width: 300px;
  height: 80px;
  border-width: 4px;
  border-style: solid;
  border-color:#7a9aed;
  border-radius: 40px;
  padding: 6px;
  align-items: center; flex-direction: row;
  margin-top: 30px;
}
.item {
  flex: 1; height: 60px;
  background-color: transparent;
  align-items: center; 
  flex-direction: row; justify-content: center;
}
.bkg {
  position: absolute;
  top: 6px;
  width: 141px; height: 60px;
  border-radius: 40px;
  background-color: #8aa7f0; 
  transform: translateX(140px);
}
.center {
  color: #ffffff;
}
</style>




