<template>
  <div class="ui-slidebar-wrap">
    <div class="ui-slidebar" bubble="true">
      <div class="prevbtn cbtn" @touchend="prevHandle" ><text class="font cbtn-font ">&#xe720;</text></div>
      <div class="slider" ref="slideWrap">
        <div class="slide-bg" ref="slideBg"></div>
        <div class="slide-bar" ref="slideBar" :style="diffwidth"></div>
        <div class="slide-btn" ref="slideBtn" :style="diffleft" @touchstart="ontouchstart" @touchmove="ontouchmove" @touchend="ontouchend"></div>
        <div class="slide-tip-wrap" ref="slideTip" :style="difftipsleft">
          <div class="arrow"></div>
          <div class="slide-show"><text class="slide-txt">{{innerVal}}</text></div>
        </div>
      </div>
      <div class="nextbtn cbtn" @touchend="nextHandle"><text class="font cbtn-font ">&#xe6df;</text></div>
    </div>
    <div class="interval">
      <text class="txt-white">{{ min }}</text>
      <text class="txt-white">{{ max }}</text>
    </div>
  </div>
</template>

<script>

export default {
  props: {
    min: {
      type: Number,
      default: ()=> {
        return 10
      }
    },
    max: {
      type: Number,
      default: ()=> {
        return 100
      }
    },
    step: {
      type: Number,
      default: ()=> {
        return 10
      }
    },
    defaultValue: {
      type: Number,
      default: ()=> {
        return 10
      }
    },
    value: {
      type: Number,
      default: ()=> {
        return 10
      }
    },
    disable: {
      type: Boolean,
      default: ()=> {
        return false;
      }
    },
  },
  data: ()=> ({
    outLeft: null,
    btnWidth: null,
    tipWidth: null,
    outWidth: null,
    flag : false,
    diffleft: {},
    diffwidth: {},
    difftipsleft: {},
    vl: null
  }),
  computed: {
    dom() {
      return weex.requireModule('dom')
    },
    innerVal: {
      set(val) {
        this.vl = val;
      },
      get() {
        return this.vl
      }
    },
    acc() {
      var m = 0;
      if(parseInt(this.min) != this.min) {
        m = String(this.min).slice(String(this.min).indexOf('.')+1).length
      }else if(parseInt(this.max) != this.max) {
        m = String(this.max).slice(String(this.max).indexOf('.')+1).length
      }else if(parseInt(this.step) != this.step){
        m = String(this.step).slice(String(this.step).indexOf('.')+1).length
      }else if(parseInt(this.value) != this.value) {
        m = String(this.value).slice(String(this.value).indexOf('.')+1).length
      }else {
        m = 0
      }
      return Math.pow(10,m)
    },
    portion() {
      let val = ((this.innerVal*this.acc - this.min*this.acc)/(this.step*this.acc) + this.step*this.acc)
      return val
    }
  },
  watch: {
    value() {
      this.innerVal = this.value;
      this.toDistance(this.portion * this.average + this.outLeft);
    }
  },
  methods: {
    ontouchstart(e) {
      // this.toDistance(e.changedTouches[0].screenX)
    },
    ontouchmove(e) {
      if(this.disable) {
        return false
      }
      if(!this.flag) {
        this.flag = true;
        this.toDistance(e.changedTouches[0].screenX)
        var that = this;
        setTimeout(function(){
          that.flag = false;
        },1000/360)
      }
    },
    ontouchend(e) {
      if(this.disable) {
        return false
      }
      this.toDistance(e.changedTouches[0].screenX);
      this.$emit('getValue', this.innerVal)
    },
    toDistance(SCREEN) {
      var btnleft, tipsleft, barwidth
      if(SCREEN <= (this.outLeft+this.average)) { 
        btnleft = 0;
        tipsleft = 0;
        barwidth = 0;
        this.innerVal = this.min;
        
      }else if( SCREEN >= (this.outLeft+this.outWidth) ){
        btnleft = this.outWidth;
        tipsleft = this.outWidth - this.btnWidth/2;
        barwidth = this.outWidth;
        this.innerVal = this.max;
      }else {
        this.innerVal = (parseInt(Math.round((SCREEN - this.outLeft)/this.average)) + this.min*this.acc -this.step*this.acc)/this.acc;
        btnleft = this.portion * this.average - this.btnWidth/2;
        tipsleft = this.portion * this.average - this.btnWidth/2;;
        barwidth = this.portion * this.average;
      }
      this.diffleft = {
        'left': btnleft + 'px'
      }
      this.diffwidth = {
        width: barwidth + 'px'
      }
      this.difftipsleft = {
        'left': tipsleft + 'px'
      }
    },
    prevHandle() {
      if(this.innerVal <= this.min || this.disable) {
        return false;
      }
      var m = this.innerVal;
      this.innerVal =( m*this.acc - this.step*this.acc)/this.acc;
      this.toDistance(this.portion * this.average + this.outLeft);
      this.$emit('getValue', this.innerVal)
    },
    nextHandle() {
      if(this.innerVal >= this.max || this.disable) {
        return false;
      }
      var m = this.innerVal;
      this.innerVal =( m*this.acc + this.step*this.acc)/this.acc;
      this.toDistance(this.portion * this.average + this.outLeft);
      this.$emit('getValue', this.innerVal)
    }
  },
  mounted () {
    this.slideBtn = this.$refs.slideBtn;
    this.slideBar = this.$refs.slideBar;
    this.slideTip = this.$refs.slideTip;
    var promiseAll = [];
    promiseAll.push(new Promise((resolve)=>{
      this.dom.getComponentRect(this.$refs.slideWrap, option => {
        this.outWrapLeft = option.size.left;
        this.outWrapWidth = option.size.width;
        resolve()
      })
    }))
    promiseAll.push(new Promise((resolve)=>{
      this.dom.getComponentRect(this.$refs.slideBg, option => {
        this.outLeft = option.size.left;
        this.outWidth = option.size.width;
        resolve()
      })
    }))
    promiseAll.push(new Promise((resolve)=>{
      this.dom.getComponentRect(this.$refs.slideBtn, option => {
        this.btnWidth =  option.size.width;
        resolve()
      })
    }))
    promiseAll.push(new Promise((resolve)=>{
      this.dom.getComponentRect(this.$refs.slideTip, option => {
        this.tipWidth =  option.size.width;
        resolve()
      })
    }))
    Promise.all(promiseAll).then(()=>{
      this.pad = this.outLeft - this.outWrapLeft;
      this.innerVal = this.value;
      this.average = this.outWidth / (((this.max*this.acc - this.min*this.acc)/(this.step*this.acc) + this.step*this.acc));
      this.toDistance(this.portion * this.average + this.outLeft);
    })
  }
}
</script>

<style scoped>
.font { font-family: iconfont2; color: white;}
.ui-slidebar-wrap {
  margin: 0 30px; padding-top: 20px;
}
.interval {
  flex-direction: row; height: 55px; padding: 0 78px; justify-content: space-between; align-items: center;
}
.txt-white { color: white; font-size: 24px;}
.ui-slidebar {
  width: 610px; flex-direction: row; justify-content: space-between; align-items: flex-end;
}
.cbtn { 
  width: 56px; height: 56px;  
  border-style: solid; border-color: white; border-width: 1px; border-radius: 56px; 
  
}
.cbtn-font { font-size: 40px; height: 56px; width: 56px; text-align: center; line-height: 56px;}

.slider { 
  width: 464px; height: 108px;
  position: relative; padding: 74px 23px 22px;
}
.slide-bg {
  width: 418px; height: 12px; border-radius: 6px;  background-color: rgba(255,255,255,.3);
  position: absolute; top: 74px; left: 23px;
}
.slide-bar {
  position: absolute; top: 74px;  left: 23px;
  height: 12px; border-radius: 6px; background-color: white;
}
.slide-btn {
  width: 46px; height: 46px; border-radius: 46px; position: absolute;
  top: 56px;
  background-color: white;
}
.slide-tip-wrap {
  width: 60px; height: 48px; position: absolute; top: 0;
}
.slide-show {
  width: 60px; height: 40px; border-radius: 8px;
  background-color: white; justify-content: center; align-items: center;
}
.slide-txt { font-size: 26px; color: #666666;}
.arrow {
  position: absolute; bottom: 3px; left: 22px;
  width: 16px; height: 16px; background-color: white;
  transform: rotate(45deg);
}
</style>



