<template>
  <div class="mid-switch" @touchstart="change">
    <div class="bg" :class="[activebg()]"></div>
    <div class="round" :class="[activeClass()]"></div>
  </div>
</template>

<script>
export default {
  props: ['val'],
  data() {
    return {
      value: false
    }
  },
  watch: {
    val() {
      this.value = this.val;
    }
  },
  methods: {
    change() {
      this.value ? this.value = false : this.value = true;
      this.$emit('increment', this.value)
    },
    activebg() {
      if(this.value) {
        return 'bg-active'
      }
      return ''
    },
    activeClass() {
      // androidios
      var cls = 'switch-active';
      if(this.platform == 'android') {
        cls = 'switch-active-a'
      }
      if(this.value) {
        return cls
      }
      return ''
    }
  },
  created () {
    this.value = this.val; // 
    this.platform = weex.config.env.platform.toLowerCase();
  }
}
</script>

<style scoped>
.mid-switch  {
  width: 110px; height: 58px;  position: relative;
  border-radius: 32px; background-color: white;
}
.bg{
  position: relative; width: 0; height: 0; border-radius: 58px;
  left: 29px; top: 29px;
  transition-property: width, backgroundColor, height, top, left;
  transition-duration: 0.3s;
  transition-delay: 0;
  transition-timing-function: cubic-bezier(0.6, -0.28, 0.735, 0.045);
}
.bg-active {
  width: 110px; height: 58px; top: 0; left: 0;
  background-color: #64bd63;
}
.round {
  width: 58px; height: 58px;  position: absolute; background-color: #dddfff;
  border-radius: 58px; top: 0; left: 0; transition: left 0.2s 'ease-out';
  box-shadow: 0 2px 1px rgba(0,0,0,.4);
}
.switch-active {
  left: 58px;
}
.switch-active-a {
  left: 51px;
}
</style>



