const myPlugin = {};
import mixin from './mixin'; 
myPlugin.install = function(Vue) {
  Vue.mixin(mixin);

  Vue.prototype.uuid = require('uuid/v1');
  Vue.prototype.animation = weex.requireModule("animation");
  Vue.prototype.modal = weex.requireModule("modal");
  Vue.prototype.mqtt = require("./mqtt.min");

  
  Vue.prototype.toast = function(config) {
    if (!this.$store.state.toastStatus) {
      this.modal && this.modal.toast(config);
      this.$store.commit('setToastStatus', true);
    }
    const _this = this;
    setTimeout(function(){
      _this.$store.commit('setToastStatus', false);
    }, 1000/60);
  };
  Vue.prototype.alert = function(config, callback) {
    this.modal && this.modal.alert(config, callback);
  };
  Vue.prototype.confirm = function(config, callback) {
    this.modal && this.modal.confirm(config, callback);
  };

  Vue.prototype.animateTo = function(el, style, callback) {
    this.animation.transition(
      el,
      {
        styles: style,
        duration: 300, //ms
        timingFunction: "ease",
        needLayout: false,
        delay: 0 //ms
      },
      function() {
        callback && callback();
      }
    );
  }
};
export default myPlugin;



