import Vue from 'vue'
import Router from "vue-router";
import App from "@/components/App";
Vue.use(Router);
const router = new Router({
  routes: [
    {
      path: "/",
      name: "App",
      component: App
    }
  ]
});
function configRouter(store) {
  
  return router;
}
module.exports = configRouter;



