<template>
  <div class="number" :style="center">
    <div v-if="model.down" class="prev arrow-item"  @touchstart="handlerPrev"><text class="iconfont">&#xe720;</text></div>
    <text class="white">{{ value }}{{ unit }}</text>
    <div v-if="model.down" class="next arrow-item"  @touchstart="handlerNext"><text class="iconfont">&#xe6f8;</text></div>
  </div>
</template>

<script>
export default {
  props: ['dataList'],
  data() {
    return {
      dvalue: 30,
      cls: false
    }
  },
  computed: {
    model() { return this.dataList.function },
    center() { if(!this.model.down) {  return { 'justify-content': 'center'}}},
    min() { return this.model.min},
    max() { return this.model.max},
    step() { return this.model.step},
    unit() { return this.model.unit},
    value() { return this.$store.state[this.model.index]}
  },
  methods: {
    handlerPrev() {
      if(this.value <= this.min) {
        this.toast({
          message: ''
        })
        return false
      }
      this.publish(this.value-this.step)
    },
    handlerNext() {
      if(this.value >= this.max) {
        this.toast({
          message: ''
        })
        return false
      }
      this.publish(this.value+this.step)
    }
  },
  created () {
    this.$store.commit('setSubject', { name: this.dataList.function.index, value: this.model.min});
  }
}
</script>

<style>
.iconfont { font-family: iconfont2; color: #ffffff; font-size: 40px;}
.white { color: #ffffff;}
.number {
  flex-direction: row; align-items: center; width: 300px;
  justify-content: space-between;
}
.arrow-item {
  width: 40px; height: 40px;
}
</style>



