<template>
  <div class="mid-enum">
    <div class="title">
      <text class="icon iconfont">&#xe661;</text>
      <text class="tit-txt">{{model.name}}</text>
    </div>
    <div class="enum-wrap">
      <text class="item" v-for="(item, index) in model.enum" :key=item.id :class="[lineStyle(index)]"  @touchstart='changeStyleHandle(index)' >{{ item }}</text>
    </div>
  </div>
</template>

<script>
export default {
  props:['dataList'],
  computed: {
    model() {
      return this.dataList.function;
    },
    value() {
      return this.$store.state[this.model.index]
    },
  },
  methods: {
    changeStyleHandle(index) {
      if(index == this.value) {
        return false;
      }
      this.publish(index);
    },
    lineStyle(index) {
      if(index == this.value) {
        return 'active'
      }
      return ''
    },
  },
  mounted () {
  }
}
</script>

<style scoped>
.mid-enum { padding-top: 30px;}
.iconfont { font-family: iconfont2; color: white; }
.icon { margin-left: 10px; font-size: 46px;}
.title { flex-direction: row; align-items: center; height: 46px; width: 670px; padding-bottom: 10px;}
.tit-txt { color: white;  font-size: 30px;  margin-left: 30px;}
.enum-wrap { padding: 20px 0 30px 63px; flex-direction: row; flex-wrap: wrap; width: 642px;}
.item { 
  padding: 0 20px; color: white; height: 60px; line-height: 60px;  border-radius: 8px; background-color: #6b94ec;
  text-overflow: ellipsis; margin: 20px 20px 0 0;
}
.active { background-color: #8cabf0;}
</style>



