<template>
  <div class="small-ui">
    <template v-for="item in dataLists">
      <small :smallStyle="smallWidth" :text="item.text" :key="item.id" ></small>
    </template>
  </div>
</template>

<style scoped>
.small-ui {
  width: 710px; flex-direction: row; height: 220px; margin-top: 18px;
  flex-direction: row; justify-content: space-between;
}
</style>

<script>
import Small from './small';
export default {
  props: ['dataLists'],
  components: {
    Small
  },
  data() {
    return {}
  },
  computed: {
    smallWidth() {
      if(this.dataLists.length == 1) {
        return {width: '710px'}
      }else if (this.dataLists.length == 2) {
        return {width: '344px'}
      }else {
        return {width: '224px'}
      }
    }
  }
}
</script>





