<template>
  <div class="mid-boolean mid-bool">
    <div class="title">
      <text class="icon icontxt">&#xe655;</text>
      <text class="icon">{{ model.name }}</text>
    </div>
    <i-switch @increment="increment" :val="value"></i-switch>
  </div>
</template>

<script>
import ISwitch from './iswitch';
export default {
  props: ['dataList'],
  data() {
    return {
      color: '#000',
    }
  },
  computed: {
    model() {
      return this.dataList.function
    },
    value() {
      return this.$store.state[this.model.index]
    }
  },
  components: {
    ISwitch
  },
  methods: {
    increment(val) {
      this.publish(val)
    }
  }
}
</script>

<style scoped>
.mid-bool {
  width: 670px; align-items: center; height: 120px;
  flex-direction: row;  justify-content: space-between;
}
.icon { font-family: iconfont2; color: #ffffff; }
.icontxt {  padding: 0 20px 0 10px;}
.title {
  height: 58px; flex-direction: row;
  align-items: center;
}
</style>



