<template>
  <div class="medium-box">
    <div :is="comp" :dataList="dataLists"></div>
  </div>
</template>

<script>
import String from './string'
import Boolean from './boolean'
import Number from './number'
import Enum from './enum'
export default {
  props: ['dataLists'],
  data() {
    return {}
  },
  components: {
    Boolean,
    Number,
    Enum,
    String
  },
  computed: {
    model() {
      return this.dataLists.function
    },
    comp() {
      if(!this.model.down) {
        return 'string'
      }
      switch(this.model.type){
        case 'BOOLEAN': return 'boolean';
        case 'NUMBER': return 'number';
        case 'ENUM': return 'enum';
        case 'STRING': return 'string';
        default: return 'string';
      }
    },
  },
  created () {
    var val;
    switch(this.model.type) {
      case 'BOOLEAN': val=false;break;
      case 'NUMBER': val=this.model.min;break;
      case 'ENUM': val=0;break;
      case 'STRING': val='';break;
      default: val='';
    }
    this.$store.commit('setSubject', { name: this.model.index, value: val});
  }
}
</script>

<style lang="less" scoped>
@import '../../css/common.less';
.medium-box {
  width: 710px; padding: 0 34px;
  align-items: center; margin-top: 18px;
  background-color: #5880e9;
}
</style>



