<template>
  <div class="show-wrap">
    <template v-for="com in componetList">
      <div v-if="com.type != 'NONE'" :is="com | computeTemp" :dataLists="com" :key="com.id"></div>
    </template>
  </div>
</template>
<script>
import BIG from './big/index'
import SMALL from './small/index'
import MIDDLE from './medium/index'
import mqtt from "@/plugins/mqtt.min";
export default {
  data() {
    return {
      componetList: [],
      args: {
        status: 'normal', // normal, abnormal
        background_color: '#00f',
        background: '',
        switch: '666dddbcaf31sdrt',
        properies: [
          {
            function: {
              index: 1,
              name: '',
              subject: 'boolean_big_btn',
              type: 'BOOLEAN',
              up: true,
              down: true,
            },
            type: 'BIG',
            icon: '',
            background: 'file',
          },
          {
            function: {
              index: 2,
              name: '',
              subject: 'number_big_btn',
              type: 'NUMBER',
              up: true,
              down: true,
              min: 10,
              max: 30,
              step: 1,
            },
            type: 'BIG',
            icon: '',
            background: 'file',
          },
          {
            function: {
              index: 3,
              name: '',
              subject: 'enum_big_btn',
              type: 'ENUM',
              up: true,
              down: true,
              enum: ['', '', '', 'ECO', '', ''],
            },
            type: 'BIG',
            icon: '',
            background: 'file',
          },
          {
            function: {
              index: 4,
              name: '',
              subject: 'string_big_btn',
              type: 'STRING',
              up: true,
              down: true,
            },
            type: 'BIG',
            icon: '',
            background: 'file',
          },
          {
            function: {
              index: 5,
              name: '',
              subject: 'boolean_middle_btn',
              type: 'BOOLEAN',
              up: true,
              down: true,
            },
            type: 'MIDDLE',
            icon: '',
            background: 'file',
          },
          {
            function: {
              index: 6,
              name: '',
              subject: 'number_middle_btn',
              type: 'NUMBER',
              up: true,
              down: true,
              min: 15,
              max: 30,
              step: 1,
            },
            type: 'MIDDLE',
            icon: '',
            background: 'file',
          },
          {
            function: {
              index: 7,
              name: '',
              subject: 'enum_middle_btn',
              type: 'ENUM',
              up: true,
              down: true,
              enum: ['', '', '', 'ECO', '', ''],
            },
            type: 'MIDDLE',
            icon: '',
            background: 'file',
          },
          {
            function: {
              index: 8,
              name: '',
              subject: 'string_middle_btn',
              type: 'STRING',
              up: true,
              down: true,
            },
            type: 'MIDDLE',
            icon: '',
            background: 'file',
          },
          {
            function: {
              index: 9,
              name: '1',
              subject: 'string_small_btn',
              type: 'STRING',
              up: true,
              down: true,
            },
            type: 'SMALL',
            icon: '',
            background: 'file',
          },
          {
            function: {
              index: 10,
              name: '2',
              subject: 'string_small_btn',
              type: 'STRING',
              up: true,
              down: true,
            },
            type: 'SMALL',
            icon: '',
            background: 'file',
          },
          {
            function: {
              index: 11,
              name: '3',
              subject: 'string_small_btn',
              type: 'STRING',
              up: true,
              down: true,
            },
            type: 'SMALL',
            icon: '',
            background: 'file',
          },
          {
            function: {
              index: 12,
              name: '4',
              subject: 'string_small_btn',
              type: 'STRING',
              up: true,
              down: true,
            },
            type: 'SMALL',
            icon: '',
            background: 'file',
          },
        ]
      }
    }
  },
  filters: {
    computeTemp (val) {
      var temp = val.type;
      if(Array.isArray(val)){
        temp = 'SMALL';
      }
      return temp
    }
  },
  computed: {
    mqttClient() {
      return this.$store.state.mqttClient
    }
  },
  components: {
    BIG,
    SMALL,
    MIDDLE
  },
  methods: {
    subscribe(topic) {
      this.$store.commit('subscribe', {
        topic: `${this.$store.getters.topic}${topic}`
      })
    },
    updateStatus(res) {
      this.$store.commit('setSubject', {
        name: res.function,
        value: res.value
      })
    },
    feedback(res) {
      const uuidList = this.$store.state.taskArr;
      if(!uuidList.get(res.task_id) && !res.result) {
        this.toast({
          message: uuidList[res.task_id] + `: `
        })
      }
      this.$store.commit('outStack', res.task_id);
    },
    mqttInit() {
      const _this = this;
      
      this.subscribe('/status'); // 
      this.subscribe('/command_resp'); // 
      this.mqttClient.on('message', function (topic, message, packet) {
        const topics = topic.slice(topic.lastIndexOf('/'));
        const msg = JSON.parse(message.toString());
        switch(topics) {
          case '/command_resp': // 
            console.log("/command_resp::", msg);
            _this.feedback(msg);
            break;
          case '/status':  // 
            console.log("/status::", msg);
            _this.updateStatus(msg);
            break; 
          case '/connection': //  
            
            break;
          case '/exception': // 

            break;
        }
        
      })
    },
    connect() {
      const _this = this;
      var href = 'broker.hivemq.com:8000/mqtt'
      const client = mqtt.connect(`ws://${href}`, {
        clientId: 'mqttjs_' + Math.random().toString(16).substr(2, 8)
      });
      client.on("connect", function() {
        _this.$store.commit('getMqttContext', client);
        _this.mqttInit();
        console.info("MQTT Serve Start");
      });

      client.on('error', function(err) {
        console.info("MQTT Serve Error", err);
      })
    },
    reduceData(value) {
      if(!value) {
        return [];
      }
      let module = value;
      // small
      module.reduce(function(accumulator, currentValue, currentIndex, array){
        if(currentValue.type == 'SMALL') {
          if(!accumulator) {
            const k = [currentValue];
            array[currentIndex] = k;
            return k 
          }else  {
            if(Array.isArray(accumulator) && accumulator.length<3) {
              accumulator.push(currentValue);
              array[currentIndex] = accumulator;
              array[currentIndex - 1] = '';
              return accumulator
                
            }else {
              const k = [currentValue];
              array[currentIndex] = k;
              return k
            }
              
          }
        }
        return currentValue;
          
      }, 0)
      module = module.filter(Boolean); // 
      return module;
    },
  },
  beforeCreate () {
    const domModule = weex.requireModule('dom');
    let platform = weex.config.env.platform.toLowerCase();
    let url;
    if ( platform.match(/[android|ios]/)) {
      url = "url('file:///font/iconfont.ttf')";
      // url = '../assets/font/iconfont.ttf';
    } else {
        url = "url('http://onx6ouezf.bkt.clouddn.com/iconfont.ttf')"
    }
    domModule.addRule('fontFace', {
      'fontFamily': "iconfont2",
      'src': "url('http://onx6ouezf.bkt.clouddn.com/iconfont.ttf')"
    })
  },
  created () {
    // connect  mqtt
    var that = this;
    const stack = new BroadcastChannel('app');
    this.componetList = this.reduceData(this.args.properies);

    stack.onmessage = function (event) {
      console.log("event.data",event.data);
      const session = event.data.properties
      const rData = that.reduceData(session);
      console.log(rData);
      that.componetList = rData;
    }
    stack.onmessageerror = function (event) {
      that.toast({
        message: 'message::' + event
      })
    }
    this.connect();
  },
}
</script>


