const myMixin = {
  methods: {
    command(command, callback) {
      this.$store.commit('publish', {
        topic: `${this.$store.getters.topic}/command`,
        message: JSON.stringify(command),
        callback
      })
      this.$store.commit('intoStack', {
        uuid: command.task_id,
        subject: this.model.name
      })
    },
    publish(val) {
      this.$store.commit('setSubject', {
        name: this.model.index,
        value: val
      });
      this.uid = this.uuid();
      this.command({
        task_id: this.uid,
        function: this.model.index,
        type: this.model.type,
        value: this.value
      })
    }
  }
}
export default myMixin


