<template>
  <div class="big-boolean">
    <template v-if="model.down">
      <ui-switch :name="name" :value="value" @attrValue="attrValue"></ui-switch>
    </template>
    <template v-else>
      <text class="center">{{ valueText }}</text>
    </template>
  </div>
</template>

<script>
import UiSwitch from './ui-switch';
export default {
  props: ['dataList'],
  components: {
    UiSwitch
  },
  data() {
    return  {
      uid: null
    }
  },
  computed: {
    model(){
      return this.dataList.function
    },
    valueText() {
      if(this.$store.state[this.model.index]) {
        return ''
      }
      return ''
    },
    value() {
      return this.$store.state[this.model.index]
    },
    name() {
      return this.model.name
    },
  },
  methods: {
    attrValue(val) {
      this.publish(val);
    }
  },
  created () {
    this.$store.commit('setSubject', { name: this.dataList.function.index, value: false});
  },
}
</script>

<style scoped>
.center {
  color: #ffffff;
}
</style>



