<template>
  <div class="ui-block">
    <title :title="model.name"></title>
    <div :is="type" :dataList="dataLists" ></div>
  </div>
</template>

<style  scoped>
.ui-block {
  width: 710px; height: 360px; margin-top: 20px;
  background-color: #5981e9;
  align-items: center; 
  justify-content: center;
}
</style>
<script>
import Title from './title';
import Boolean from './boolean.vue';
import Number from './number.vue';
import String from './string.vue';
import Enum from './enum.vue';

export default {
  props: ['dataLists'],
  data () {
    return {
      
    }
  },
  components: {
    Title,
    Boolean,
    Number,
    String,
    Enum
  },
  computed: {
    model() {
      return this.dataLists.function
    },
    type() {
      switch(this.dataLists.function.type){
        case 'BOOLEAN': return 'boolean';
        case 'NUMBER': return 'number';
        case 'ENUM': return 'enum';
        case 'STRING': return 'string';
        default: return 'string';
      }
    }
  }
}
</script>






