<template>
  <div class="big-enum" :class="[downCenter(model.down)]">
    <template v-if="model.down">
      <text class="white item"  v-for="(item, index) in model.enum" :class="[lineStyle(index)]" :ref="'active-' + index" :key="item.id" @touchstart="changeState(index)">{{ item }}</text>
    </template>
    <template v-else>
      <div class="item active" ><text class="white">{{ model.enum[value] }}</text></div>
    </template>
    <div class="round" ><text class="font white">&#xe65c;</text></div>
  </div>
</template>

<script>
export default {
  props: ['dataList'],
  data() {
    return {
      val: null
    }
  },
  created () {
    this.val = 0;
    this.$store.commit('setSubject', { name: this.dataList.function.index, value: 0});
  },
  computed: {
    model() {
      return this.dataList.function
    },
    value() {
      return this.$store.state[this.model.index]
    },
  },
  methods: {
    changeState(index) {
      if(index == this.value) {
        return false;
      }
      this.publish(index);
    },
    lineStyle(index) {
      if(index == this.value) {
        return 'active'
      }
      return 'n-active'
    },
    downCenter(v) {
      if(v) {
        return ''
      }
      return 'center'
    }
  }
}
</script>

<style scoped>
.big-enum {
  flex-direction: row; padding: 1px 64px 0; width: 710px; flex-wrap: wrap;
  position: relative;
}
.center { justify-content: center; }
.item { 
  padding: 0 20px; height: 60px; line-height: 60px; border-radius: 10px;  margin: 20px 20px 0 0; color: #ffffff;
  align-items: center; flex-direction: row; justify-content: center; transition: 'background-color' 500;
}
.n-active {
  background-color: #6a8eec;
}
.active {
  background-color: #8ba8f0; padding: 0 40px;
}
.round {
  border-radius: 60px; width: 60px; height: 60px; background-color: #8ba8f0;
  position: absolute; right: 200px; top: -80px;
}
.white { color: #ffffff; text-align: center;}
.font { font-family: iconfont2; line-height: 60px;}
</style>



