<template>
  <div class="string">
    <text class="center" >{{ value }}</text>
  </div>
</template>

<script>
export default {
  props: ['dataList'],
  computed: {
    model() {
      return this.dataList.function
    },
    value() {
      return this.$store.state[this.model.index]
    },
  },
  created () {
    this.$store.commit('setSubject', { name: this.dataList.function.index, value: 'kkk'});
  }
}
</script>

<style scoped>
.center { color: #ffffff;}
</style>



