export default function(Vue) {
  return {
    state: {
      mqttClient: null,
      device_sn: 1,
      product_id: '5adc44dcc090a9002b04385c',
      taskArr: new Map(),
      toastStatus: false
    },
    mutations: {
      setToastStatus(state, val) {
        state.toastStatus = val;
      },
      intoStack(state, { uuid, name}) {
        state.taskArr.set(uuid, name);
      },
      outStack(state, uuid) {
        state.taskArr.delete(uuid);
      },
      setSubject(state, {name, value}) {
        Vue.set(state, name,value);
      },
      getMqttContext(state, context) {
        state.mqttClient = context;
      },
      subscribe({ mqttClient }, { topic, callback }) {
        mqttClient && mqttClient.subscribe(topic, callback);
      },
      publish({ mqttClient }, { topic, message, callback}) {
        mqttClient && mqttClient.publish(topic, message, callback);
      }
    },
    actions: {
      
    },
    getters: {
      topic({ product_id, device_sn}) { 
        return `client/products/${product_id}/devices/${device_sn}`
      }
    }
  };
}



